.masonry-page {
  text-align: center;
  height: 100vh;
  width: 100vw;
  padding: 0 25px;
  overflow: scroll;
  // overflow-x: hidden;
  position: relative;

  .masonry {
    position: relative;
    height: 100%;

    &-list {
      position: relative;
      height: 100%;

      &-item {
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}



