.container {
  background-image: url(/src/asset/flandre_1.jpg);
  position: absolute;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: all 0.5s;
  min-height: 450px;
  width: 100%;
  height: 450px;
  top: 0;
  left: 0;
}

.container:before {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.15);
  content: "";
}

.ctn-nav {
  position: relative;
  top: 0;
  z-index: 90;
  width: 100%;
  height: 60px;
  padding: 16px;
  display: flex;
}
.nav-info {
  -webkit-box-flex: 1;
  -moz-box-flex: 1;
  -o-box-flex: 1;
  box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  color: var(--light-grey);
  height: 100%;
}
.nav-site-name {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.15);
  font-weight: bold;
  text-align: center;
  line-height: 60px;
}

.ctn-pageInfo {
  width: 100%;
  position: absolute;
  bottom: 150px;
  padding: 0 8%;
  text-align: center;
}
.pageInfo-title {
  margin-bottom: 8px;
  color: var(--white);
  font-weight: normal;
  font-size: 2.5em;
  line-height: 1.5;
  -webkit-line-clamp: 3;
}
.pageInfo-desc {
  margin: 8px 0;
  color: var(--white);
  font-weight: normal;
  font-size: 1.25rem;
  line-height: 1.5;
  -webkit-line-clamp: 3;
}

.pageInfo-button {
  /* bg-white hover:bg-cyan-600 p-3 rounded-lg text-slate-950 cursor-pointer */
  background-color: white;
  border-radius: 0.5rem;
  padding: 0.75rem;
  color:black;
  cursor: pointer;
}
.pageInfo-button:hover {
  background-color: rgb(195, 174, 174);
}

@media screen and (max-width: 768px) {
  .container {
    height: 360px;
    min-height: 360px;
  }
  .ctn-pageInfo {
    bottom: 125px;
  }
}
